"use client";

import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';

interface Message {
  id: string;
  content: string;
  role: 'user' | 'assistant';
  timestamp: Date;
}

interface UsageInfo {
  remaining: number;
  total: number;
  exceeded: boolean;
  resetTime: string;
}

export default function ChatUI() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [usage, setUsage] = useState<UsageInfo>({
    remaining: 100,
    total: 100,
    exceeded: false,
    resetTime: '24 hours'
  });

  // Generate a simple user ID for this session
  const [userId] = useState(() => `user_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`);

  // Load usage information on component mount
  useEffect(() => {
    fetchUsageInfo();
  }, []);

  const fetchUsageInfo = async () => {
    try {
      const response = await fetch('/api/blackbox-proxy', {
        method: 'GET',
        headers: {
          'x-user-id': userId,
        },
      });
      
      if (response.ok) {
        const data = await response.json();
        setUsage(data.usage);
      }
    } catch (err) {
      console.error('Failed to fetch usage info:', err);
    }
  };

  const sendMessage = async () => {
    if (!inputMessage.trim() || isLoading) return;

    const userMessage: Message = {
      id: `msg_${Date.now()}`,
      content: inputMessage.trim(),
      role: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/blackbox-proxy', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-user-id': userId,
        },
        body: JSON.stringify({
          messages: [
            ...messages.map(msg => ({
              role: msg.role,
              content: msg.content
            })),
            {
              role: 'user',
              content: userMessage.content
            }
          ]
        }),
      });

      const data = await response.json();

      if (response.status === 429) {
        setError(data.message || 'Usage limit exceeded. Please try again tomorrow.');
        setUsage(prev => ({ ...prev, exceeded: true, remaining: 0 }));
        return;
      }

      if (!response.ok) {
        throw new Error(data.message || 'Failed to get AI response');
      }

      // Update usage information if provided
      if (data.usage) {
        setUsage(data.usage);
      }

      // Add AI response to messages
      const aiMessage: Message = {
        id: `msg_${Date.now()}_ai`,
        content: data.response || data.message || 'No response received',
        role: 'assistant',
        timestamp: new Date()
      };

      setMessages(prev => [...prev, aiMessage]);

    } catch (err) {
      console.error('Error sending message:', err);
      setError(err instanceof Error ? err.message : 'An unexpected error occurred');
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const clearChat = () => {
    setMessages([]);
    setError(null);
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      {/* Header with Usage Information */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle className="text-2xl font-bold">Secure AI Chat</CardTitle>
            <div className="flex items-center space-x-4">
              <Badge variant={usage.exceeded ? "destructive" : usage.remaining < 10 ? "secondary" : "default"}>
                {usage.remaining}/{usage.total} messages remaining
              </Badge>
              <Button variant="outline" size="sm" onClick={clearChat}>
                Clear Chat
              </Button>
            </div>
          </div>
        </CardHeader>
      </Card>

      {/* Error Alert */}
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Usage Warning */}
      {usage.remaining < 10 && !usage.exceeded && (
        <Alert>
          <AlertDescription>
            You have {usage.remaining} messages remaining. Usage resets in {usage.resetTime}.
          </AlertDescription>
        </Alert>
      )}

      {/* Chat Messages */}
      <Card className="min-h-96">
        <CardContent className="p-6">
          <div className="space-y-4 mb-4 max-h-96 overflow-y-auto">
            {messages.length === 0 ? (
              <div className="text-center text-gray-500 py-8">
                <p className="text-lg">Welcome to Secure AI Chat</p>
                <p className="text-sm mt-2">Your messages are protected with usage limits to prevent abuse.</p>
              </div>
            ) : (
              messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div
                    className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                      message.role === 'user'
                        ? 'bg-blue-500 text-white'
                        : 'bg-gray-100 text-gray-900'
                    }`}
                  >
                    <p className="text-sm">{message.content}</p>
                    <p className="text-xs mt-1 opacity-70">
                      {message.timestamp.toLocaleTimeString()}
                    </p>
                  </div>
                </div>
              ))
            )}
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-gray-100 text-gray-900 max-w-xs lg:max-w-md px-4 py-2 rounded-lg">
                  <p className="text-sm">AI is thinking...</p>
                </div>
              </div>
            )}
          </div>

          {/* Input Area */}
          <div className="flex space-x-2">
            <Input
              value={inputMessage}
              onChange={(e) => setInputMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={usage.exceeded ? "Usage limit reached" : "Type your message..."}
              disabled={isLoading || usage.exceeded}
              className="flex-1"
            />
            <Button 
              onClick={sendMessage} 
              disabled={isLoading || !inputMessage.trim() || usage.exceeded}
            >
              {isLoading ? 'Sending...' : 'Send'}
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Security Information */}
      <Card>
        <CardContent className="p-4">
          <div className="text-sm text-gray-600 space-y-2">
            <p className="font-semibold">Security Features:</p>
            <ul className="list-disc list-inside space-y-1 text-xs">
              <li>Server-side usage enforcement prevents bypass attempts</li>
              <li>Daily message limits protect against abuse</li>
              <li>All requests are proxied through secure endpoints</li>
              <li>Usage tracking is session-based and automatically resets</li>
            </ul>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
